@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;margin:0;padding:0}.App{height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;background:#f9f9fb;font-family:'Poppins', sans-serif;position:relative}.App__Container{max-width:90%}.btn-grad{background-image:linear-gradient(to right, #48d9c2 0%, #6760fd 51%, #48d9c2 100%)}.btn-grad{border:none;margin:10px;padding:15px 45px;text-align:center;transition:0.5s;background-size:200% auto;color:white;box-shadow:0 0 20px #eee;border-radius:25px;display:block;cursor:pointer}.btn-grad:focus{outline:none}.btn-grad:hover{background-position:right center;color:#fff;text-decoration:none}

.StoreTable{margin-top:60px;border-spacing:0px 5px;border-collapse:separate}.StoreTable td{font-size:0.9rem}.StoreTable th,.StoreTable td{padding:0px 15px;max-width:200px;text-align:left}.StoreTable tr{height:50px;background-color:#fff;margin-bottom:5px;transition:all 0.5s ease-in-out}.StoreTable thead tr:first-child{background:none;cursor:unset;vertical-align:text-top}.StoreTable tr:hover{background:#6760fd;background:linear-gradient(298deg, rgba(103,96,253,0.4) 0%, rgba(72,217,194,0.4) 100%)}.StoreTable thead tr:first-child:hover{background:none}.StoreTable tr td:first-child{border-top-left-radius:5px}.StoreTable tr td:last-child{border-top-right-radius:5px}.StoreTable tr td:first-child{border-bottom-left-radius:5px;width:180px;text-align:center;cursor:pointer}.StoreTable tr td:last-child{border-bottom-right-radius:5px}.StoreTable tbody>tr:hover .StoreTable__StoreName{display:block;padding:5px;background-color:#48d9c2;color:#fff;border-radius:5px;text-align:center}

.AddStoreModal{position:absolute;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.308);justify-content:center;align-items:center;display:flex}.AddStoreModal .AddStoreModal__container{width:700px;background-color:white;border-radius:10px;position:relative;padding:50px 30px}.AddStoreModal .AddStoreModal__title{font-size:1.8rem}.AddStoreModal .AddStoreModal__titleIcon{margin-right:15px;height:21px}.AddStoreModal .AddStoreModal__closeBtn{position:absolute;right:15px;top:15px;height:50px;width:50px;display:flex;align-items:center;justify-content:center;border-radius:50%;cursor:pointer;transition:background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms}.AddStoreModal .AddStoreModal__closeBtn:hover{background-color:#ececec}.AddStoreModal .AddStoreModal__form{margin:40px 0}.AddStoreModal .AddStoreModal__form .AddStoreModal__formGroup{margin-bottom:30px}.AddStoreModal .AddStoreModal__form label{font-size:0.8rem;color:#424242}.AddStoreModal .AddStoreModal__form .AddStoreModal__formInputs{display:block;width:100%;font-size:1rem;padding:8px 0;border:none;border-bottom:1px solid #646464;transition:all 0.2s ease}.AddStoreModal .AddStoreModal__form .AddStoreModal__formInputs:focus{outline:none;border-image-source:linear-gradient(#48d9c2, #6760fd);border-width:2px;border-image-slice:1}

